<template>
    <div class="card mt-2  ml-6 col-5">
        <form class="m-5">
            <div class="row">
                <div class="form-group col">
                <label>{{label_name}}*</label>
                <input type="texte" class="form-control" v-model="form.name" :placeholder="label_name" required>
                </div>
                <div class="form-group col">
                <label>{{label_surname}}*</label>
                <input type="texte" class="form-control" v-model="form.surname" :placeholder="label_surname" required>
                </div>
            </div>
            <div class="row">
                <div class="form-group col">
                    <label>{{label_email}}</label>
                    <input type="email" class="form-control" v-model="form.email" :placeholder="label_email">
                </div>
                <div class="form-group col">
                    <label>{{label_phone}}*</label>
                    <input type="number" class="form-control" v-model="form.phone" :placeholder="label_phone" required>
                </div>
                <div class="form-group col" style="display:flex; flex-direction: column;">
                    <label>{{label_sexe}}*</label>
                    <el-select v-model="form.sexe">
                        <el-option v-for="item in optionsSexe" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </div>
            </div>
            <div class="row">
                <div class="form-group col">
                    <label>{{label_town}}</label>
                    <input type="email" class="form-control" v-model="form.ville" :placeholder="label_town">
                </div>
                <div class="form-group col">
                    <label>{{label_street}}</label>
                    <input type="texte" class="form-control" v-model="form.rue" :placeholder="label_street">
                </div>
                <div class="form-group col">
                    <label>{{label_quater}}</label>
                    <input type="texte" class="form-control" v-model="form.quartier" :placeholder="label_quater">
                </div>
                <div class="form-group col">
                    <label>{{label_postal}}</label>
                    <input type="texte" class="form-control" v-model="form.code_postal" :placeholder="label_postal">
                </div>
            </div>
            
            
            <el-button type="primary" class="mt-5" @click="handlesave">{{label_save}}</el-button>
        </form>
    
    </div>
</template>

<script>

import axios from 'axios';
// import moment from 'moment'; 
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

export default {
    

    data() {
        return {

            // lang
            label_name: this.$t('pages.customer.customer_add.name'),
            label_surname: this.$t('pages.customer.customer_add.surname'),
            label_postal: this.$t('pages.customer.customer_add.postal'),
            label_email: this.$t('pages.customer.customer_add.email'),
            label_phone: this.$t('pages.customer.customer_add.phone'),
            label_quater: this.$t('pages.customer.customer_add.quater'),
            label_street: this.$t('pages.customer.customer_add.street'),
            label_sexe: this.$t('pages.customer.customer_add.sexe'),
            label_town: this.$t('pages.customer.customer_add.town'),
            label_save: this.$t('pages.customer.customer_add.save'),

            optionsSexe: [{
                    value: 'M',
                    label: 'Masculin'
                },
                {
                    value: 'F',
                    label: 'Feminin'
                },
            ],

            all_cust : [],
            search: '',
            form : {
                code_postal : "",
                email : "",
                id: null,
                name: "",
                phone: null,
                profile: null,
                quartier : "",
                rue: "",
                sexe : "",
                surname : "",
                tags : "",
                type_id: null,
                username : "",
                ville : "",
                tags: ""
            },

            // Empty
            code_postal : "",
            email : "",
            id: null,
            name: "",
            phone: null,
            profile: null,
            quartier : "",
            rue: "",
            sexe : "",
            surname : "",
            tags : "",
            type_id: null,
            username : "",
            ville : "",
            tags: ""
        }
    },
    
    created() {
        
    },


    methods: {

        async handlesave() {
            NProgress.start();

            this.form.tags = this.form.name+" / "+this.form.surname+" / "+this.form.email+" / "+this.form.phone+" / "+this.form.quartier+" / "+this.form.ville+" / "+this.form.rue+" / ";

            if ((this.form.name != "" && this.form.name != " " ) || (this.form.name.length < 2)) {
                try {

                    console.log(this.form);
                    await axios.post('xpress/client/Add', this.form);
                    this.$notify({
                        type: 'success',
                        title: this.$t('save')
                    });

                } 
                catch (error) {
                    this.$notify({
                        type: 'error',
                        title: this.$t('save-error')
                    });
                }
            } 
            else {

                this.$notify({
                    type: 'error',
                    title: this.$t('save')
                });
            }
            this.emptyForm();
            NProgress.done();
        },

        emptyForm(){

            this.form.code_postal= "",
            this.form.email= "",
            this.form.id= null,
            this.form.name= "",
            this.form.phone= null,
            this.form.profile= null,
            this.form.quartier= "",
            this.form.rue= "",
            this.form.sexe= "",
            this.form.surname = "",
            this.form.tags= "",
            this.form.type_id= null,
            this.form.username= "",
            this.form.ville= "",
            this.form.tags= "",

            console.log(this.form);
        }
    },
}
</script>